import React from 'react'

const TimeLine = ({ item }) => {
    return (
        <div className='flex items-center border-l border-pantone/30 pl-4 pt-2 gap-x-2
        after:inline-block
        after:w-2
        after:h-2
        after:bg-pantone
        after:rounded-full
        after:absolute
        after:left-[-3.5px]
        '>

            <div className='px-1 py-1 bg-pantone/30 rounded-lg text-xs min-w-[72px]'>{item.year}</div>
            <div>{item.info}</div>
        </div>
    )
}

export default TimeLine