import { useState } from 'react';
import { useSelector } from 'react-redux';
import Modal from '../components/Modal';
import Progress from '../components/Progress';
import TimeLine from '../components/TimeLine';
import { Icon } from '../icons';
import { data } from '../data'
const About = () => {

    const [isOpen, setIsOpen] = useState(false);

    const { profilePicture } = useSelector(state => state.about);
    const { isDark } = useSelector(state => state.theme);
    const { lang } = useSelector(state => state.language);

    return (
        <div className='max-w-7xl grid grid-cols-1 md:grid-cols-2 items-center justify-center mx-auto md:h-[calc(100vh-100px)] gap-4 px-4'>
            <div className='max-w-[560px] mx-auto '>
                <img src={profilePicture} alt="burakcakir" className='md:rounded-xl' />
            </div>
            <div className='pb-20 md:pb-0 md:pt-0 pt-10 overflow-y-scroll md:overflow-y-hidden h-min'>
                <h1 className={`md:text-4xl text-2xl font-semibold ${isDark ? 'text-white' : 'text-black'}`}>Burak <span className={`uppercase px-2 text-pantone ${isDark ? '' : 'bg-black/50 rounded-xl'}`}>Çakır</span></h1>
                <h2 className={`text-xl md:text-2xl ${isDark ? 'text-gray-300' : 'text-gray-700'} py-4 font-semibold border-b border-dashed border-gray-300`}><span>{data[lang].position}</span></h2>
                <p className={`text-base mt-4 font-semibold ${isDark ? 'text-gray-300' : ''}`}>
                    {data[lang].aboutPrefix}<span className={`px-2 text-pantone rounded-md ${isDark ? '' : 'bg-black/50'}`}>Burak Çakır.</span>{data[lang].aboutDescription}
                </p>
                <div className='mt-5 text-right'>
                    <button onClick={() => setIsOpen(!isOpen)}>{data[lang].aboutMoreBtn}</button>
                </div>
            </div>
            <Modal isOpen={isOpen} isDark={isDark}>
                <div className={`relative transform overflow-hidden rounded-lg text-left shadow-xl transition-all sm:my-8 sm:w-full sm:max-w-2xl md:max-w-3xl ${isDark ? 'bg-black/70' : 'bg-gray-300'}`}>
                    <div className='grid grid-cols-1 md:grid-cols-[auto_500px] w-[360px] md:w-full'>
                        <div className={`px-[30px] py-[30px] relative ${isDark ? 'bg-black/90' : 'bg-gray-300'}`}>
                            <div onClick={() => setIsOpen(!isOpen)} className={`${isDark ? '' : 'bg-black'} md:hidden absolute right-3 top-3 text-pantone rotate-45 border border-pantone p-1 rounded-3xl cursor-pointer hover:border-white hover:text-white duration-300`}>
                                <Icon name="close" />
                            </div>
                            <div className='flex justify-center w-full'>
                                <img className='rounded-full object-cover w-[120px] h-[120px]' src={profilePicture} alt="/" />
                            </div>
                            <div className='text-center'>
                                <h1 className={`${isDark ? 'text-white' : 'text-black'} text-2xl font-semibold  py-4`}>Burak <span className={`${isDark ? '' : 'bg-black/70'} px-1 rounded-lg text-pantone`}>Çakır</span></h1>
                            </div>
                            <ul className={`text-sm flex flex-col gap-y-2 divide-y divide-dashed ${!isDark && 'divide-black'}`}>
                                <li className='flex items-center gap-x-2 text-pantone font-semibold'><span className={`${!isDark && 'bg-black'} p-2 rounded-full`}><Icon name="computer" /></span> <span className={`${isDark ? 'text-white' : 'text-black'}`}>{data[lang].position}</span></li>
                                <li className='flex items-center gap-x-2 text-pantone pt-4 font-semibold'><span className={`${!isDark && 'bg-black'} p-2 rounded-full`} ><Icon name="user" /></span> <span className={`${isDark ? 'text-white' : 'text-black'}`}>15.06.1992</span></li>
                                <li className='flex items-center gap-x-2 text-pantone pt-4 font-semibold'><span className={`${!isDark && 'bg-black'} p-2 rounded-full`} ><Icon name="location" /></span> <span className={`${isDark ? 'text-white' : 'text-black'}`}>{data[lang].location}</span></li>
                                <li className='flex items-center gap-x-2 text-pantone pt-4 font-semibold'><span className={`${!isDark && 'bg-black'} p-2 rounded-full`} ><Icon name="phone" /> </span><span className={`${isDark ? 'text-white' : 'text-black'}`}>+90 541 621 50 73</span></li>
                                <li className='flex items-center gap-x-2 text-pantone pt-4 font-semibold'><span className={`${!isDark && 'bg-black'} p-2 rounded-full`} ><Icon name="mail" /></span> <span className={`${isDark ? 'text-white' : 'text-black'}`}>burak2043@gmail.com</span></li>
                            </ul>
                        </div>
                        <div className='px-[30px] py-[30px] max-h-[550px] bg-black/70 scrollbar-thin scrollbar-thumb-pantone/50 scrollbar-track-black'>
                            <div className=''>
                                <h1 className='text-white text-lg border-b border-dashed border-pantone pb-2'>{data[lang].about}</h1>
                                <p className='text-white pt-2'>
                                    {data[lang].aboutPrefix} <span className='text-pantone/80'>Burak Çakır</span>. {data[lang].aboutDescription}
                                </p>
                            </div>
                            <div className='mt-2 text-white'>
                                <h3 className='text-lg font-semibold'>{data[lang].aboutModalSkill.split(" ")[0]} <span className='text-pantone'>{data[lang].aboutModalSkill.split(" ")[1]}</span></h3>
                                <ul className='flex flex-col gap-y-2'>
                                    <li><Progress title="ReactJS" point={20} /></li>
                                    <li><Progress title="Laravel" point={40} /></li>
                                    <li><Progress title="NextJS" point={40} /></li>
                                    <li><Progress title="NodeJS" point={50} /></li>
                                </ul>
                            </div>
                            <div className='mt-2 text-white relative'>
                                <h3 className='text-lg font-semibold'>{data[lang].aboutModalEducation.split(" ")[0]} <span className='text-pantone'>{data[lang].aboutModalEducation.split(" ")[1]}</span></h3>
                                {
                                    data[lang].aboutModalEducationList.map(item => (
                                        <TimeLine key={item.id} item={item} />
                                    ))
                                }
                            </div>
                            <div className='mt-2 text-white relative'>
                                <h3 className='text-lg font-semibold'>{data[lang].aboutModalWorking.split(" ")[0]} <span className='text-pantone'>{data[lang].aboutModalWorking.split(" ")[1]}</span></h3>
                                {
                                    data[lang].aboutModalWorkingList.map(item => (
                                        <TimeLine key={item.id} item={item} />
                                    ))
                                }
                            </div>
                        </div>
                    </div>
                    <div className={`text-pantone md:flex hidden bg-opacity-50 justify-end py-2 ${!isDark && 'bg-black'}`}>
                        <span className='border border-pantone rounded-full rotate-45 p-2 hover:border-white hover:text-white duration-300 cursor-pointer' onClick={() => setIsOpen(!isOpen)} >
                            <Icon name="close" />
                        </span>
                    </div>
                </div>
            </Modal>
        </div>
    )
}

export default About