import React, { useState } from 'react'
import Writing from '../components/Writing'
import cv from '../assets/pdf/Profile.pdf';
import Modal from '../components/Modal';
import { useSelector } from 'react-redux';
import { Icon } from '../icons';
import { data } from '../data';

const Home = () => {
    const [isOpen, setIsOpen] = useState(false);
    const { certificate } = useSelector(state => state.about);
    const { isDark } = useSelector(state => state.theme);
    const { lang } = useSelector(state => state.language);


    function handleCertificateModal(e) {
        e.preventDefault();
        setIsOpen(!isOpen);
    }


    return (
        <div className='max-w-7xl flex items-center justify-center mx-auto h-[calc(100vh-100px)] px-4'>
            <div className='text-center'>
                <h1 className={` text-5xl md:text-7xl font-semibold ${isDark ? 'text-white' : ''}`}>Burak <span className={`uppercase text-pantone ${isDark ? '' : 'bg-black/50 px-2 rounded-xl'}`}>Çakır</span></h1>
                <h2 className={`text-xl md:text-2xl  py-4 font-semibold ${isDark ? 'text-white' : 'text-black'}`}><span className={isDark ? 'text-gray-400' : 'text-gray-600'}>Full Stack</span> {data[lang].position}</h2>
                <Writing isDark={isDark} />
                <a href={cv} target="_blank" rel="noreferrer" className='button mr-2'>{data[lang].cvButton}</a>
                <a href={cv} target="_blank" rel="noreferrer" className='button' onClick={handleCertificateModal}>{data[lang].certificateButton}</a>
            </div>
            <Modal isOpen={isOpen}>
                <div className='max-w-7xl mx-auto px-4'>
                    <div className='mb-3 w-full flex justify-end'>
                        <button className='py-1 px-2' onClick={handleCertificateModal}><Icon name="close2" /></button>
                    </div>
                    <div className='grid grid-cols-2 sm:grid-cols-3 md:grid-cols-4 lg:grid-cols-5 gap-4'>
                        {
                            certificate.map(item => (
                                <a href={item.pdf} target="_black" key={item.id} className="">
                                    <img src={item.image} alt={item.platform} className="object-contain rounded-lg" />
                                    <p className={`text-pantone mt-2 ${isDark ? 'bg-black/70 rounded-lg' : ''}`}>{item.platform}</p>
                                </a>
                            ))
                        }
                    </div>
                </div>
            </Modal>
        </div>
    )
}

export default Home