import React from 'react';
import { Icon } from '../icons';
import { useSelector } from 'react-redux';

const Contact = () => {
    const { mail, phone, socialMedia, address, sendMailBtn } = useSelector(state => state.about);
    const { lang } = useSelector(state => state.language)

    const { isDark } = useSelector(state => state.theme);

    return (
        <div className='max-w-7xl flex items-center justify-center mx-auto h-[calc(100vh-100px)] px-4 pb-20 md:pb-0'>
            <div className='w-full'>
                <h1 className={`${isDark ? 'border-pantone text-white' : 'border-black'} text-3xl font-semibold border-b border-dashed mb-4 pb-2`}>İletişim <span className={`${!isDark && 'bg-black/50'} px-2 rounded-lg text-pantone`}>Bilgileri</span></h1>
                <div className='text-white grid grid-cols-1 md:grid-cols-2 gap-x-4'>
                    <div>
                        <ul>
                            <li className='contact-list'><Icon name="location" /> <span className='ml-2'>{address[lang]}</span></li>
                            <li className='contact-list'><Icon name="phone" /><span className="ml-2">{phone}</span></li>
                            <li className='contact-list'><Icon name="mail" /><span className="ml-2">{mail}</span></li>
                            <li className='flex justify-center w-full bg-pantone/80 p-2 rounded-full text-black'>
                                <ul className='flex justify-center w-full'>
                                    {
                                        socialMedia.map(item => (
                                            <li key={item.id} className='px-2 py-1'><a className='hover:text-white duration-200' href={item.url} rel="noreferrer" target="_blank"><Icon name={item.icon} /></a></li>
                                        ))
                                    }
                                </ul>
                            </li>
                        </ul>
                    </div>
                    <div className='flex flex-col w-full items-center justify-center md:mt-0 mt-10'>
                        <div className={`${isDark ? 'text-pantone' : 'text-black/50'} mb-6`}><Icon name="plane" size={70} /></div>
                        <a href={`mailto:${mail}`} className='px-4 py-2 bg-pantone/80 hover:bg-pantone duration-300 text-black font-semibold rounded'>{sendMailBtn[lang]}</a>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Contact