import React, { useRef, useCallback } from 'react'
import { Swiper, SwiperSlide } from 'swiper/react';
import { Pagination } from "swiper";
import 'swiper/css';
import { useSelector } from 'react-redux';
import SwiperCard from '../components/SwiperCard';
import { Icon } from '../icons';
import { data as langdata } from '../data';

const Portfolio = () => {

    const { data } = useSelector(state => state.portfolio);
    const { isDark } = useSelector(state => state.theme);
    const { lang } = useSelector(state => state.language);


    const sliderRef = useRef(null);
    const handlePrev = useCallback(() => {
        if (!sliderRef.current) return;
        sliderRef.current.swiper.slidePrev();
    }, []);

    const handleNext = useCallback(() => {
        if (!sliderRef.current) return;
        sliderRef.current.swiper.slideNext();
    }, []);

    return (
        <div className='max-w-7xl flex items-center flex-col justify-center mx-auto h-[calc(100vh-100px)] px-4 pb-20 md:pb-0'>
            <div className='w-full'>
                <h1 className={`${isDark && 'text-white'} text-3xl font-semibold border-b border-pantone border-dashed`}>{langdata[lang].portfolioHeader.split(" ")[0]} <span className={`${isDark ? '' : 'bg-black/50 text-pantone rounded-xl'} px-2`}>{langdata[lang].portfolioHeader.split(" ")[1]}</span></h1>
            </div>
            <Swiper
                ref={sliderRef}
                spaceBetween={30}
                className="max-w-7xl w-full mt-8"
                breakpoints={{
                    576: { slidesPerView: 2, },
                    768: { slidesPerView: 2, },
                    992: { slidesPerView: 3, },
                }}
                pagination={{
                    clickable: true,
                }}
                modules={[Pagination]}
            >
                {
                    data[lang].map(item => (
                        <SwiperSlide key={item._id}><SwiperCard item={item} /></SwiperSlide>
                    ))
                }
            </Swiper>
            <div className='text-white w-full text-right mt-2'>
                <span className={`slide-arrow ${!isDark && 'hover:bg-black'}`} onClick={handlePrev}><Icon name="prev" /></span>
                <span className={`slide-arrow ${!isDark && 'hover:bg-black'} ml-2`} onClick={handleNext}><Icon name="next" /></span>
            </div>
            <div>
            </div>
        </div>
    )
}

export default Portfolio