import React from 'react'
import { Switch, Route } from 'react-router-dom';
import { useSelector } from 'react-redux';

// import bg from './assets/img/bg.jpg';
// import bgLight from './assets/img/bgLight.jpg';
import gradient from './assets/img/gradient.jpeg';
import gradient1 from './assets/img/gradient1.jpeg';
import Header from './components/Header'
import Home from './pages/Home';
import About from './pages/About';
import Portfolio from './pages/Portfolio';
import PortfolioDetail from './pages/PortfolioDetail';
import Contact from './pages/Contact';


const App = () => {

  const { isDark } = useSelector(state => state.theme);

  return (
    <div className={`w-full min-h-screen object-cover bg-contain`} style={{ backgroundImage: `url(${isDark ? gradient : gradient1})`, backgroundSize: 'cover' }}>
      {/* <div className='tracker'>
        <span>x</span>
      </div> */}
      <Header />
      <Switch>
        <Route path="/" exact>
          <Home />
        </Route>
        <Route path="/hakkimda" exact>
          <About />
        </Route>
        <Route path="/iletisim" exact>
          <Contact />
        </Route>
        <Route path="/portfolyo" exact>
          <Portfolio />
        </Route>
        <Route path="/portfolyo/:slug" exact>
          <PortfolioDetail />
        </Route>

      </Switch>
    </div>
  )
}

