import React from 'react';

const Modal = ({ isOpen, children, isDark }) => {
    return (
        <div className={`relative z-10 ${isOpen ? 'opacity-100 visible' : 'opacity-0 invisible'} duration-500 `} aria-labelledby="modal-title" role="dialog" aria-modal="true">
            <div className={`fixed inset-0 bg-opacity-90 transition-opacity ${isDark ? 'bg-black' : 'bg-black/70'}`}>

            </div>

            <div className="fixed inset-0 z-10 overflow-y-auto">
                <div className="flex min-h-full items-center justify-center p-4 text-center sm:items-center sm:p-0">
                    {children}
                </div>
            </div>
        </div>
    )
}

export default Modal