import React from 'react';
import { Link } from 'react-router-dom';

const SwiperCard = ({ item }) => {

    return (
        <div className='relative group'>
            <div className='overflow-hidden rounded-3xl'>
                <img className='w-full hover:scale-105 duration-300 min-h-[264px]' src={item?.image} alt={item?.title} />
            </div>
            <div>
                <Link to={`/portfolyo/${item.slug}`} className='absolute px-2 py-1 font-semibold -bottom-9 left-5 duration-150 group-hover:bottom-5 text-black bg-pantone rounded-3xl'>{item?.title}</Link>
            </div>
        </div>
    )
}

export default SwiperCard