import { createSlice } from "@reduxjs/toolkit";

const themeSlice = createSlice({
    name: "theme",
    initialState: {
        isDark: true,
    },
    reducers: {
        changeTheme: (state) => {
            state.isDark = !state.isDark
        }
    },
    extraReducers: {

    }
})

export const { changeTheme } = themeSlice.actions
export default themeSlice.reducer