import { createSlice } from "@reduxjs/toolkit";
import { data } from './data'

const portfolioSlice = createSlice({
    name: 'portfolio',
    initialState: {
        data: data,
        selected: {},
        loading: true
    },
    reducers: {
        selectPortfolio: (state, action) => {
            state.selected = data["TR"].find(item => item.slug === action.payload);
            state.loading = false
        }
    },
    extraReducers: {}
});

export const { selectPortfolio } = portfolioSlice.actions;
export default portfolioSlice.reducer;