import React from 'react'

const Progress = ({ title, point }) => {
    return (
        <>
            <p>{title}</p>
            <div className="w-full h-[2px] bg-pantone/10">
                <div className={`bg-pantone h-full`} style={{ width: `${point}%` }}></div>
            </div>
        </>
    )
}

export default Progress