import React from 'react';
import Typed from 'typed.js'

const Writing = ({isDark}) => {
    const el = React.useRef(null);
    const typed = React.useRef(null);
    React.useEffect(() => {
        const options = {
            strings: [
                'HTML 5',
                'CSS 3',
                'JavaScript',
                'ReactJS',
                'React Native',
                'NodeJS',
                'ExpressJS',
                'MongoDB',
                'Mysql',
            ],
            typeSpeed: 75,
            backSpeed: 90,
            loop : true
        };
        typed.current = new Typed(el.current, options);
        return () => {
            typed.current.destroy();
        }
    }, [])

    return (
        <div className={`pb-4 text-xl md:text-3xl ${isDark ? 'text-white' : 'text-black'}`}>
            <span ref={el} />
        </div>

    );
}

export default Writing