import React, { useEffect } from 'react';
import { useParams, useHistory } from 'react-router-dom';
import { selectPortfolio } from '../redux/portfolio/portfolioSlice';
import { useSelector, useDispatch } from 'react-redux';
import { Icon } from '../icons';
import { data } from '../data'

const PortfolioDetail = () => {
    const { slug } = useParams();
    const { isDark } = useSelector(state => state.theme);
    const { lang } = useSelector(state => state.language);

    const history = useHistory();
    const dispatch = useDispatch();


    const selectItem = data[lang].portfolioDetail.find(item => item.slug === slug);

    useEffect(() => {
        dispatch(selectPortfolio(slug));
    }, [selectItem])

    return (
        <div className='max-w-7xl flex items-center flex-col justify-center mx-auto h-[calc(100vh-100px)] px-4'>
            <div className={`flex gap-x-1 md:hidden text-pantone w-full mb-3`} onClick={() => history.goBack()}>
                <div className={`flex gap-2 py-1 px-2 ${!isDark && 'bg-black/50 rounded-lg'}`}>
                    <span><Icon name="arrowleftcircle" /></span>
                    <span>Geri</span>
                </div>
            </div>
            <div className='w-full'>
                <h1 className={`${isDark ? 'text-white border-pantone' : 'border-black'} text-xl md:text-3xl font-semibold border-b  border-dashed mb-6 pb-2`}>{selectItem.title}</h1>
            </div>
            <div className={`${isDark ? 'text-white' : ''} grid grid-cols-1 md:grid-cols-2 w-full gap-8 font-semibold`}>
                <div>
                    <img src={selectItem.image} className='rounded-xl' alt="" />
                </div>
                <div className='flex items-center'>
                    <p className='text-base'>{selectItem.description}</p>
                </div>
            </div>
        </div>
    )
}

export default PortfolioDetail