const Icon = ({ name, size = 24, fill }) => {
    const icons = {
        moon: MoonIcon,
        menu: MenuIcon,
        computer: ComputerIcon,
        user: UserIcon,
        location: LocationIcon,
        phone: PhoneIcon,
        mail: MailIcon,
        close: CloseIcon,
        prev: PrevIcon,
        next: NextIcon,
        facebook: FacebookIcon,
        instagram: InstagramIcon,
        linkedin: LinkedinIcon,
        github: GithubIcon,
        plane: PlaneIcon,
        home: HomeIcon,
        info: InfoIcon,
        task: TaskIcon,
        mailbold: MailBoldIcon,
        arrowleftcircle: ArrowLeftCircleIcon,
        close2: CloseIcon2,
        sun: SunIcon
    }

    const Component = icons[name];
    return <Component size={size} fill={fill} />
}

export {
    Icon
}