import { useState, useEffect } from 'react';
import { Link, NavLink/* , useLocation */ } from 'react-router-dom';
import { Icon } from '../icons';
import { changeTheme } from '../redux/theme/themeSlice';
import { changeLang } from '../redux/language/languageSlice'
import { useDispatch, useSelector } from 'react-redux';
import ReactFlagsSelect from "react-flags-select";

const Header = () => {

    // const { pathname } = useLocation();
    const dispatch = useDispatch();
    const { isDark } = useSelector(state => state.theme);

    const dil = useSelector(state => state.language);

    const [lang, setLang] = useState(dil.lang);

    function setTheme() {
        dispatch(changeTheme());
    }

    function setLanguage(dil) {
        setLang(dil)
        dispatch(changeLang(dil))
    }

    const menuData = {
        TR: [
            {
                name: "Anasayfa",
                link: "/",
                icon: "home"
            },
            {
                name: "Hakkımda",
                link: "/hakkimda",
                icon: "info"
            },
            {
                name: "Porfolyo",
                link: "/porfolyo",
                icon: "task"
            },
            {
                name: "İletişim",
                link: "/iletisim",
                icon: "mailbold"
            }
        ],
        US: [
            {
                name: "Home",
                link: "/",
                icon: "home"
            },
            {
                name: "About",
                link: "/hakkimda",
                icon: "info"
            },
            {
                name: "Porfolio",
                link: "/portfolyo",
                icon: "task"
            },
            {
                name: "Contact",
                link: "/iletisim",
                icon: "mailbold"
            }
        ]
    }

    const themeName = {
        TR: {
            dark: "Koyu",
            light: "Açık"
        },
        US: {
            dark: "Dark",
            light: "Light"
        }
    }
    useEffect(() => {
        localStorage.setItem("lang", lang);
    }, [lang])

    return (
        <div className='max-w-7xl mx-auto px-4'>
            <div className='flex items-center w-full justify-between md:py-8 py-4 px-4 md:px-0 relative'>
                <div className={`${isDark ? 'text-white' : 'text-black'} text-3xl duration-300 cursor-pointer text-center md:text-right`}><Link to="/">bÇ</Link></div>
                <ul className={`${isDark ? 'text-white' : 'text-black'} font-semibold md:flex hidden h-full w-full max-w-md gap-x-6 justify-end`}>
                    {
                        menuData[lang].map(menu => (
                            <li key={menu.name} className='hover:text-pantone duration-300'><NavLink exact activeClassName="header-active" to={menu.link}>{menu.name}</NavLink></li>

                        ))
                    }
                </ul>
                <div className='flex gap-x-1'>
                    <ReactFlagsSelect
                        selected={lang}
                        onSelect={code => setLanguage(code)}
                        countries={["US", "TR"]}
                        customLabels={{ US: "EN", TR: "TR" }}
                        placeholder="Lang"
                        className="!p-0 bg-pantone font-semibold rounded-lg"
                        showSecondarySelectedLabel={false}

                    />
                    <button className='flex text-black items-center' onClick={setTheme}>
                        {isDark ? <Icon name="sun" fill="black" size={18} /> : <Icon name="moon" fill="black" size={18} />}
                        <span className='ml-1 text-sm'>{isDark ? themeName[lang].dark : themeName[lang].light}</span>
                    </button>
                </div>
                {/* Mobile Menu */}
                <div className='fixed block md:hidden bottom-0 left-0 right-0 bg-pantone text-black rounded-t-2xl'>
                    <ul className='flex justify-between'>
                        {
                            menuData[lang].map(menu => (
                                <li key={menu.name} className='p-1'>
                                    <NavLink to={menu.link} exact className='flex flex-col px-3 py-1 rounded-xl text-sm font-semibold' activeClassName='selected-link'>
                                        <span className='flex justify-center w-full'><Icon name={menu.icon} /></span>
                                        <span className='text-xs'>{menu.name}</span>
                                    </NavLink>
                                </li>
                            ))
                        }
                        {/* <li className='p-2'>
                            <NavLink to="/" exact className='flex flex-col p-1 rounded-xl text-sm font-semibold' activeClassName='selected-link'>
                                <span className='flex justify-center w-full'><Icon name="home" /></span>
                                <span>Anasayfa</span>
                            </NavLink>
                        </li>
                        <li className='p-2'>
                            <NavLink to="/hakkimda" exact className='flex flex-col p-1 rounded-xl text-sm font-semibold' activeClassName='selected-link'>
                                <span className='flex justify-center w-full'><Icon name="info" /></span>
                                <span>Hakkımda</span>
                            </NavLink>
                        </li>
                        <li className='p-2'>
                            <NavLink to="/portfolyo" exact activeClassName='selected-link' className={`flex flex-col p-1 rounded-xl text-sm font-semibold ${pathname.substring(0, 10) === '/portfolyo' ? 'selected-link' : ''}`}>
                                <span className='flex justify-center w-full'><Icon name="task" /></span>
                                <span>Portfolyo</span>
                            </NavLink>
                        </li>
                        <li className='p-2'>
                            <NavLink to="/iletisim" exact activeClassName='selected-link' className='flex flex-col p-1 rounded-xl text-sm font-semibold'>
                                <span className='flex justify-center w-full'><Icon name="mailbold" /></span>
                                <span>İletişim</span>
                            </NavLink>
                        </li> */}
                    </ul>
                </div>
            </div>
        </div>
    )
}

export default Header